.card{background:var(--bg-card);border:1px solid var(--border-color);border-radius:8px;overflow:hidden;transition:transform .2s ease,box-shadow .2s ease,border-color .2s ease;display:flex;flex-direction:column;height:100%}.card:hover{transform:translateY(-4px);box-shadow:var(--shadow-card);border-color:var(--primary-color)}.card.selected{border:2px solid var(--primary-color)}.imageWrapper{height:200px;padding:20px;background:var(--bg-hover);display:flex;justify-content:center;align-items:center}@media(max-width: 480px){.imageWrapper{height:150px}}.imageWrapper img{max-height:100%;max-width:100%;object-fit:contain;mix-blend-mode:multiply}.content{padding:16px;display:flex;flex-direction:column;flex-grow:1}.brand{font-size:.75rem;color:#6b778c;text-transform:uppercase;font-weight:600;margin-bottom:4px}.title{font-size:1.125rem;color:var(--text-main);margin:0 0 12px 0;font-weight:600}.features{list-style:none;padding:0;margin:0 0 16px 0;font-size:.875rem;color:#42526e;flex-grow:1}.features li{margin-bottom:4px;padding-left:12px;position:relative}.features li::before{content:"•";position:absolute;left:0;color:var(--primary-color)}.footer{display:flex;justify-content:space-between;align-items:center;margin-top:auto}.price{font-weight:700;font-size:1.125rem;color:var(--text-main)}.compareBtn{background:var(--primary-bg-light);border:1px solid var(--primary-color);color:var(--primary-color);padding:8px 16px;border-radius:4px;font-weight:600;cursor:pointer;transition:all .2s;font-size:.875rem}.compareBtn:hover:not(:disabled){background:rgba(var(--primary-color), 0.1)}.compareBtn:disabled{border-color:#ebecf0;color:#a5adba;cursor:not-allowed}.compareBtn.btnActive{background:var(--primary-color);color:#fff}.compareBtn.btnActive:hover{background:var(--primary-color)}